@import "../mixins";
.header{
  padding: 1.5em 0;
  z-index: 20;
  top: 0;
  width: 100%;
  position: fixed;
  transition: all .3s;
  &._fix{
    background: rgba(#FFFFFF,.9);
    box-shadow: 0 .25em 1em rgba(0, 51, 153, 0.04), 0 .125em .125em rgba(0, 51, 153, 0.08);
    backdrop-filter: blur(3.125em);
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__info{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-weight: 500;
    text-transform: uppercase;
    &:not(:last-child){
      margin-right: 6em;
    }
  }
}

.burger{
  display: none;
}

.header2{
  padding: 1.5em 0;
  margin-top: -4.5em;
  &__logo{
    width: 12.75em;
    display: block;
    img{
      width: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1em 0;
    &__address{
      display: none;
    }
    &__mob{
      position: absolute;
      top: 3.75em;
      left: 0;
      width: 100%;
      height: calc(100vh - 3.75em);
      background-color: #fff;
      padding: 1.875em 1.25em 3.5em;
      display: flex;
      flex-direction: column;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      overflow-y: auto;
      overflow-x: hidden;
    }
    &__info{
      display: block;
      margin-top: auto;
      padding-top: 2em;
      .footer__socials{
        margin-top: 2em;
      }
    }
    &._open{
      .header__mob{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        background-image: url(../img/burger2.svg);
      }
    }
  }

  .nav{
    flex-direction: column;
    &__link{
      font-weight: 500;
      font-size: 1.5em;
      margin: 0 auto 2em 0 !important;
    }
  }

  .burger{
    display: flex;
    width: 2em;
    height: 2em;
    background-image: url(../img/burger.svg);
    @include bgImgSize(100%);
  }

  .header2{
    margin-top: -3.75em;
    padding: .5em 0;
    &__logo{
      width: 8em;
    }
  }
}
