.error{
  padding: 1em 0;
  &__img{
    width: 30.9375em;
    max-width: 100%;
    margin-bottom: .625em;
    img{
      width: 100%;
    }
  }
  &__desc{
    margin-top: 3em;
    p{
      font-size: 1.5em;
      line-height: 150%;
      font-family: $font2;
      letter-spacing: -.0416em;
      font-weight: 300;
    }
  }
}


@media screen and (max-width: 992px){
  .error{
    &__desc{
      margin-top: 1em;
      p{
        font-size: 1em;
      }
    }
  }
}
