@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    font-size: 1em;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-outline{
    border: 1px solid $black;
    color: $black;
    font-family: $font2;
    &:hover{
        background-color: $black;
        color: #fff;
    }
}
