.display1,
.display2,
.display3,
.display4,
.display5,
.display6{
  font-family: $font2;
  font-weight: 200;
  line-height: 125%;
}

.display1{
  font-size: 7.5em;
  letter-spacing: -.017em;
}

.display2{
  font-size: 5em;
  letter-spacing: -.025em;
}

.display3{
  font-size: 4em;
  letter-spacing: -.03125em;
}

.display4{
  font-weight: 300;
  font-size: 2em;
}

.display5{
  font-weight: 500;
  font-size: 1.625em;
}

.display6{
  font-weight: 500;
  font-size: 1.25em;
}

.display16{
  font-family: $font2;
  font-size: 1em;
  line-height: 150%;
  font-weight: 300;
  letter-spacing: -.03125em;
}


@media screen and (max-width: 992px){
  .display2,
  .display3{
    font-size: 2em;
  }
}
