.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    &__bg{
        background: rgba(0, 0, 0, 0.66);
        position: fixed;
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &--active{
        opacity: 1;
        pointer-events: unset;
        .modal__bg{
            display: block;
        }
    }
    &__dialog{
        background: #FAFAFA;
        border-radius: 1.5em;
        width: 79.25em;
        max-width: calc(100% - 1.25em);
        overflow-y: auto;
        max-height: 100vh;
        padding: 4em;
        position: relative;
        z-index: 3;
    }
    &__close{
        position: absolute;
        right: 1.5em;
        top: 1.5em;
        width: 2em;
        img{
            width: 100%;
        }
    }
    &__title{
        font-weight: 200;
        font-family: $font2;
        font-size: 2.5em;
        line-height: 150%;
        letter-spacing: -.05em;
        span{
            font-weight: 400;
        }
    }
    &__form{
        margin-top: 3em;
        &-grid{
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 3em 1.87em;
        }
        ._full{
            grid-column: 1/-1;
        }
    }
    &__nav{
        display: flex;
        justify-content: center;
        margin-top: 2.5em;
    }
    &__hint{
        margin-top: 2em;
        font-weight: 300;
        font-size: 1.25em;
        line-height: 150%;
        letter-spacing: -.04em;
    }
}

.thank-modal{
    .modal__dialog{
        width: 38em;
    }
    .contacts-thanks{
        width: 100%;
        max-width: 100%;
        margin: 0;
    }
}


@media screen and (max-width: 992px) {
    .modal {
        &__dialog {
            border-radius: 0;
            height: 100vh;
            max-width: 100%;
            width: 100%;
            padding: 3em 1em;
        }
        &__close{
            top: 1em;
            right: 1em;
        }
        &__title{
            font-size: 1.5em;
            span{
                display: block;
            }
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__form{
            &-grid{
                grid-template-columns: 100%;
                gap: 1.5em;
            }
        }
        &__hint{
            font-size: 1em;
        }
    }
}
