@import "../mixins";
.footer{
  &__inner{
    padding: 3.125em 0 9.25em;
    border-top: 1px solid rgba(#E2E3E6,.8);
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__c,
  &__mail,
  &__address{
    font-size: 1em;
    line-height: 150%;
    font-weight: 300;
    letter-spacing: -.03125em;
  }
  &__c{
    margin-right: auto;
  }
  &__info{
    margin-right: 7.5em;
    display: flex;
    align-items: center;
  }
  &__address{
    margin-left: 2em;
  }
  &__socials{
    display: flex;
    align-items: center;
    &-item{
      width: 2.5em;
      &:not(:last-child){
        margin-right: .625em;
      }
      img{
        max-width: 100%;
        transition: all .3s;
      }
      &:hover{
        transform: translateY(-.45em);
      }
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__inner{
      border: none;
      padding: 0 0 3.125em;
    }
    &__inner{
      @include grid100gap(2em);
    }
    &__info{
      margin-right: 0;
      display: block;
    }
    &__address{
      margin: 1em 0 0;
    }
  }
}
