@import "../mixins";
.glasses{
  padding: .875em 0 5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 18.75em / 4));
    gap: 1.875em 6.25em;
  }

}

.glass-card{
  @include center;
  &__images{
    @include center;
    width: 100%;
    position: relative;
  }
  img{
    max-width: 100%;
    max-height: 100%;
    transition: all .3s;
  }
  ._view2{
    opacity: 0;
    @include centerAbsolute;
  }
  &:hover{
    ._view1{
      opacity: 0;
    }
    ._view2{
      opacity: 1;
    }
  }
}

.catalog{
  padding: 5em 0 1.875em;
  &__top{
    max-width: 68.75em;
  }
  &__grid{
    margin-top: 2em;
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 1.875em);
  }
  &__nav{
    background: #FAFAFA;
    border-radius: 1.5em;
    padding: 1.875em;
    margin-top: 1.25em;
    @include center;
  }
  &__btn{
    height: 3.125em;
    padding: 0 1.875em;
    font-weight: 500;
    font-family: $font2;
    text-transform: uppercase;
    span{
      font-size: 1.125em;
      letter-spacing: .055em;
    }
  }
}

.catalog-card{
  padding: 3em 2em 2em;
  background: #FAFAFA;
  border-radius: 1.5em;
  margin: 1.875em 1.875em 0 0;
  width: calc(50% - 1.875em);
  position: relative;
  @media screen and (min-width: 993px){
    &._big{
      width: calc(66% - 1.875em);
    }
    &._small{
      width: calc(34% - 1.875em);
      .catalog-card__detail{
        ._view1{
          display: none;
        }
        ._view2{
          display: unset;
        }
      }
    }
  }
  &._open{
    .catalog-card__info{
      opacity: 1;
      pointer-events: unset;
    }
    .swiper-pagination{
      opacity: 0;
    }
  }
  &__brand{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-img{
      height: 3em;
      background: #FAFAFA;
      img{
        max-height: 100%;
        max-width: 100%;
        mix-blend-mode: multiply;
      }
    }
    &-title{
      text-transform: uppercase;
      text-align: right;
      max-width: 11.875em;
      a{
        &:hover{
          color: $gray;
        }
      }
    }
  }
  &__media{
    height: 37em;
    width: calc(100% + 4em);
    margin-left: -2em;
    padding: 2em 0;
    position: relative;
    &-nav{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      @include center;
    }
  }
  .swiper{
    &-container{
      overflow: hidden;
      width: 100%;
      height: 100%;
    }
    &-wrapper{
      height: 100%;
    }
    &-slide{
      height: 100%;
    }
    &-button{
      position: static;
      margin: 0 .25em;
      width: 1.5em;
      height: auto;
      transition: all .3s;
      opacity: .5;
      img{
        width: 100%;
      }
      &:hover{
        transform: scale(1.5);
        opacity: 1;
      }
    }
    &-pagination{
      display: none;
    }
  }
  &__img{
    @include center;
    background: #FAFAFA;
    height: 100%;
    width: 100%;
    img{
      display: block;
      max-width: 100%;
      max-height: 100%;
      mix-blend-mode: multiply;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 5em;
  }
  &__price{
    font-weight: 500;
    font-family: $font2;
    font-size: 1.375em;
    letter-spacing: .045em;
  }
  &__buttons{
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 0 1.875em;
    height: 3.125em;
    text-transform: uppercase;
    font-family: $font2;
    font-weight: 500;
    margin-left: 1.25em;
    span{
      font-size: 1.375em;
      letter-spacing: .045em;
    }
  }
  &__detail{
    font-weight: 300;
    color: $black;
    span{
      font-size: 1.125em;
    }
    ._view2{
      display: none;
    }
    &:hover{
      color: $gray;
    }
  }
  &__info{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: inherit;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 3em;
    .catalog-card__brand-img{
      flex-shrink: 0;
    }
    .catalog-card__brand-title{
      max-width: 100%;
      width: 100%;
      text-align: left;
      br{
        display: none;
      }
    }
    &-wrap{
      max-height: calc(100% - 6em);
      padding: 2em 0;
      width: 100%;
      position: relative;
      overflow: hidden;
      &:before,
      &:after{
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        height: 1px;
        width: 100%;
        max-width: 21.875em;
        background: #2A3640;
        opacity: 0.33;
      }
      &:before{
        top: 0;
      }
      &:after{
        bottom: 0;
      }
    }
    &-nav{
      margin-top: 1.5em;
      display: flex;
      justify-content: center;
      .catalog-card__btn{
        margin: 0;
        height: 3.75em;
        width: 15.625em;
        max-width: 100%;
        padding: 0;
      }
    }
    &-close{
      position: absolute;
      top: 3em;
      right: 2em;
      width: 1.5em;
      height: 1.5em;
      &:hover{
        transform: scale(1.3);
      }
      img{
        width: 100%;
        transition: all .3s;
      }
    }
  }
  &__text{
    text-align: center;
    @include grid100gap(1.5em);
    padding: 0 2em;
    p{
      font-size: .9375em;
      line-height: 160%;
      letter-spacing: -.053em;
    }
  }
}

.brands{
  padding: 1.875em 0 3em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(8,calc(12.5% - ((.9375em * 7) / 8)));
    gap: .9375em;
  }
}

.brand-card{
  background: #FAFAFA;
  border-radius: 1.5em;
  &__img{
    padding-bottom: 100%;
    position: relative;
    background-color: inherit;
    border-radius: inherit;
    img{
      mix-blend-mode: multiply;
      @include centerAbsolute;
      max-width: calc(100% - 1.875em);
      max-height: calc(100% - 1.875em);
    }
  }
}

.why{
  padding: 3em 0 5em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-left{
      max-width: 39em;
    }
    &-right{
      max-width: 47.0625em;
    }
  }
  &__desc{
    font-size: 1.5em;
    line-height: 150%;
    font-weight: 300;
    letter-spacing: -.042em;
  }
  &__grid{
    margin-top: 6em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em));
    gap: 3em;
  }
}

.why-card{
  max-width: 23.4375em;
  &__img{
    height: 9.25em;
    display: flex;
    align-items: flex-end;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    margin-top: 1.875em;
    .display4{
      text-transform: uppercase;
    }
  }
  &__desc{
    margin-top: 1em;
    font-weight: 300;
    font-size: 1.25em;
    line-height: 150%;
    & *{
      letter-spacing: .04em;
      line-height: inherit;
    }
  }
}

.contacts{
  padding: 5em 0 4em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.5em 1.875em;
  }
  .display3{
    max-width: 9em;
  }
  &__form{
    &-grid{
      margin-top: .625em;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 1.875em;
    }
    ._full{
      grid-column: 1/-1;
    }
  }
  &__check{
    margin-top: 1.875em;
    max-width: 41.75em;
  }
  &__nav{
    margin-top: 2.5em;
    display: flex;
  }
  &__btn{
    padding: 0 1.875em;
    height: 3.75em;
    font-weight: 500;
    text-transform: uppercase;
    &:hover{
      path{
        fill:#fff;
      }
    }
    span{
      font-size: 1.375em;
    }
    svg{
      width: 1.5em;
      height: auto;
      margin-left: .5em;
    }
    ._view2{
      display: none;
    }
  }
}

.map{
  min-height: 100%;
  height: 50em;
  border-radius: 1.5em;
  overflow: hidden;
}

.contacts-thanks{
  max-width: 30em;
  margin: 6.5em auto 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__desc{
    margin-top: 1.875em;
    p{
      font-size: 1.5em;
      line-height: 125%;
      letter-spacing: -.0416em;
    }
  }
  &__btn{
    padding: 0 1.875em;
    height: 3.75em;
    margin-top: 2.5em;
    font-weight: 500;
    span{
      font-size: 1.375em;
      letter-spacing: .045em;
    }
  }
}


@media screen and (max-width: 992px){
  .glasses{
    padding: .625em 0 2em;
    &__grid{
      grid-template-columns: repeat(3,calc(33.33% - 1em));
      gap: 1.5em;
    }
    ._hide-tablet{
      display: none;
    }
  }

  .catalog{
    padding: 2em 0;
    &__grid{
      width: calc(100% + 1em);
    }
    &__nav{
      margin-top: 1.5em;
      background-color: transparent;
      padding: 0;
    }
    &__btn{
      width: 20.625em;
      padding: 0;
      max-width: 100%;
    }
  }

  .catalog-card{
    padding: 1.5em .75em .75em;
    width: calc(50% - 1em);
    margin: 1em 1em 0 0;
    &__brand{
      &-img{
        height: 1.5em;
      }
    }
    &__media{
      height: 18.75em;
      padding: 2.5em 0;
      width: calc(100% + 3em);
      margin-left: -1.5em;
      &-nav{
        display: none;
      }
    }
    .swiper{
      &-pagination{
        bottom: 0;
        display: flex;
        justify-content: center;
        &-bullet{
          background: #F2F2F2;
          opacity: 1;
          width: .5em;
          height: .5em;
          margin: 0 .25em !important;
          z-index: 1;
          &-active{
            background: $gray;
          }
        }
      }
    }
    &__img{
      align-items: flex-end;
    }
    &__bottom{
      flex-direction: column;
      margin-top: 2.75em;
    }
    &__price{
      font-size: 1.5em;
      line-height: 125%;
    }
    &__buttons{
      margin-top: 1.375em;
      flex-direction: column-reverse;
      align-items: center;
    }
    &__btn{
      margin: 0;
      span{
        font-size: 1.125em;
      }
    }
    &__detail{
      span{
        font-size: .875em;
      }
    }
    &__info{
      padding: 1.5em .75em .75em;
      &-close{
        top: .75em;
        right: .75em;
      }
      &-wrap{
        padding: 1em 0;
        max-height: calc(100% - 4em);
      }
      &-nav{
        margin-top: 1em;
        .catalog-card__btn{
          height: 3.125em;
        }
      }
    }
    &__text{
      padding: 0;
      gap: 1em;
      p{
        font-size: .75em;
        //@include truncate(5);
        &:nth-child(1)~p{
          //display: none;
        }
      }
    }
  }

  .brands{
    padding: 2em 0;
    &__grid{
      grid-template-columns: repeat(4,calc(25% - ((.9375em * 3) / 4)));
    }
  }

  .why{
    padding: 2em 0;
    &__top{
      @include grid100gap(1.5em)
    }
    &__desc{
      font-size: 1.25em;
      line-height: 135%;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 2em;
    }
  }

  .why-card{
    &__img{
      height: unset;
    }
    &__title{
      margin-top: 1.25em;
      .display4{
        font-size: 1.75em;
      }
    }
  }

  .contacts{
    padding: 2em 0 4em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__form{
      &-grid{
        margin-top: 1.5em;
        gap: 1.25em;
      }
    }
  }

  .map{
    min-height: unset;
    height: 40em;
    width: calc(100% + 1.25em);
    margin-left: -.625em;
  }

  .contacts-thanks{
    margin-top: 2em;
    &__desc{
      margin-top: 1.5em;
      p{
        font-size: 1em;
      }
    }
    &__btn{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .glasses{
    &__grid{
      grid-template-columns: 100%;
      gap: 2em;
    }
    ._mob-hide{
      display: none;
    }
  }

  .glass-card{
    img{
      max-height: 6.25em;
    }
    &._logo{
      img{
        max-height: 12.5em;
      }
    }
  }

  .catalog{
    &__grid{
      width: 100%;
    }
  }

  .catalog-card{
    width: 100%;
    margin-right: 0;
    padding: 2.125em 1.5em 1.5em;
    &__info{
      padding: 2.125em 1.5em 1.5em;
      &-close{
        top: 1.5em;
        right: 1.5em;
      }
    }

  }

  .brands{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - (.9375em / 2)));
    }
  }

  .contacts{
    &__form{
      &-grid{
        grid-template-columns: 100%;
      }
    }
    &__check,
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      width: 100%;
      ._view1,
      svg{
        display: none;
      }
      ._view2{
        display: unset;
      }
    }
  }
}
