.conf{
  padding: 2.25em 0;
}

.text{
  margin-top: 3em;
  max-width: 52.5em;
  &>*{
    font-size: 1.25em;
    line-height: 150%;
    font-weight: 300;
    letter-spacing: .04em;
  }
}


@media screen and (max-width: 992px){
  .conf{
    padding: 1em 0;
  }

  .text{
    margin-top: 1.5em;
    &>*{
      font-size: 1em;
    }
  }
}
