.fg {
  position: relative;
  margin: 0;
  textarea,
  input {
    height: 3.89em;
    font-size: 1.125em;
    line-height: 120%;
    border: none;
    border-bottom: 1px solid rgba($black,.1);
    padding: 0;
    width: 100%;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: #000;
    }
    &.input-active,
    &:focus {
      &+label{
        font-size: .75em;
        line-height: 167%;
        top: -.42em;
      }
    }
  }
  textarea{
    height: 10em;
    resize: none;
    padding-top: 1.22em;
    padding-bottom: 1.22em;
  }
  label{
    position: absolute;
    left: 0;
    top: 1.22em;
    transition: all .3s;
    font-size: 1.125em;
    line-height: 144%;
    width: 100%;
    color: rgba($black,.5);
    pointer-events: none;
    span{
      color: $black;
    }
  }
  &__hint,
  &__error{
    position: absolute;
    font-size: .75em;
    line-height: 167%;
    top: -.42em;
    right: 0;
    pointer-events: none;
  }
  &__error{
    color: $red;
    opacity: 0;
    transition: all .3s;
  }
  &__hint{
    color: $primary;
  }
  &._error{
    .fg__hint{
      opacity: 0;
    }
    .fg__error{
      opacity: 1;
    }
    input,
    textarea{
      border-color: $red;
    }
    label{
      color: $red;
      span{
        color: inherit;
      }
    }
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        border-color: $black;
        background-color: $black;
        background-image: url(../img/check.svg);
      }
    }
  }
  label {
    padding-left: 1.75em;
    position: relative;
    min-height: 1.5em;
    font-size: 1em;
    line-height: 150%;
    cursor: pointer;
    display: block;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border: 1px solid #A9AEB2;
      border-radius: .25em;
      background-size: 1.125em;
      background-position: center;
      background-repeat: no-repeat;
      width: 1.25em;
      height: 1.25em;
      left: 0;
      top: 0;
      transition: all .100s;
    }
  }
  a{
    position: relative;
    z-index: 2;
    text-decoration: underline;
    white-space: nowrap;
  }
}


@media screen and (max-width: 992px){
  .fg{
    textarea{
      height: 13.33em;
    }
  }
}
